import React from 'react';
import { AiOutlineRight } from 'react-icons/ai';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import AuthHeader from 'views/mobileView/components/molecules/Headers/AuthHeader';

export default function NameScreen(props) {

  const [name, setName] = React.useState('');
  const { state } = props.location;

  React.useEffect(() => {
    if (state === null || state === undefined) {
      props.history.push('/signup')
    }
  }, [])

  const validateInput = () => {
    return name.length > 1
  }

  const onNextBtnPressed = async () => {
    props.history.push({
      pathname: '/verify-otp1',
      state: { ...state, name }
    })
  };

  return (
    <div className="mainContainer">
      <AuthHeader navigation={props.history} left='login' />
      <div className="dataContainer">
        <div className="login-header">
          <div className="bigHeader">Hello there!</div>
          <div className="bodyTextDark" style={{ marginTop: "6px" }}>Please enter your first name</div>
        </div>
        <FloatingTextInput
          label="First name"
          autoFocus
          value={name}
          autoCapitalize="words"
          onChange={(e) => {
            let a = e.target.value.replace(/[^a-zA-Z]/g, '');
            setName(a)
          }}
        />
      </div>
      <div className="login-footer">
        <button className="solidButton" style={{ opacity: validateInput() ? 1 : 0.4 }}
          onClick={() => { validateInput() && onNextBtnPressed() }}>
          Next <AiOutlineRight size={12} />
        </button>
      </div>
    </div>
  )
}
